body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #D8E5EC;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* navbar */
.logo{
  color: #00D186;
}
.logo-title{
  font-size: 1.5rem;
  font-weight: 700;
}
.Links{
  font-size: 1.2rem;
  font-weight: 400;
}
.contact{
  background-color: #00D186;
  border-radius: 20px;
  padding: 8px 24px;
  border: none;
  width: -webkit-max-content;
  width: max-content;
  font-size: 18px;
  color: white;
}
/* Navbar */

/* Home */
.home-title{
  font-size: 52px;
  color: #333333;
}
.align-center{
  margin-top: 10px;
}
/* Home */
/* Qualification */
.qualification{
  background: #00273E;
  padding: 56px 10%;
  width: 100.8%;
  color: white;
  text-align: center;
}
/* Qualification */
/* experience */
.buton{
  background-color: #066d34;
  width: -webkit-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: max-content;
  padding: 10px;
  border-radius: 50%;
}
/* experience */
/* Awards */
.award{
  background-color: #00273E;
}

.award-card{
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.4);
  color: #DDDDDD;
}

/* Awards */
/* research and publication */
.shadow{
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.4);
}
.hover-effect:hover{
  -webkit-transform: scale(110%);
          transform: scale(110%);
  transition: all 0.5s ease-in-out;
}
/* research and publication */
/* form */
.form{
  display: flex;
flex-direction: column;
align-items: flex-start;
padding: 24px 32px;
grid-gap: 16px;
gap: 16px;

position: absolute;
width: 400px;
height: 332px;
left: 50px;
bottom: 20px;

background: linear-gradient(111.95deg, #00C47E 0%, #001B2A 100%);
box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
}
.form input,textarea{
  width: 100%;
  padding: 10px 0;
  text-indent: 20px;
}
/* form */
/* footer */
.footer{
  background-color: #00273E;
  height: 200px;
  position: relative;
  margin-top: 200px;
}
/* footer */

@media only screen and (max-width: 1005px) {
  .footer{
    height: 650px;
    text-align: center;
  }
}
@media only screen and (max-width: 1005px){
  .form{
    width: 99%;
    left: 0px;
  }
}



